import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { UtilityServiceService } from 'src/app/services/utility-service.service';
import { SignalRService } from './services/signalR.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
})
export class AppComponent implements OnInit {
  constructor(
    private utilityServiceService: UtilityServiceService,
    private router: Router,

  ) {
    this.broadcastChannel();
  }
  ngOnInit(): void {
    //if (!sessionStorage.getItem('token')) {
    //  console.log('aaaaaaaaaaaaaaaaaaaaaaaaa')
    //  return;
    //}
  }
  broadcastChannel() {
    let splittedArr = window.location.origin.split('.');
    let matchValue = '';
    if (splittedArr.length > 0)
      matchValue = `.${splittedArr[splittedArr.length - 2]}.${splittedArr[splittedArr.length - 1]}`;
    else
      matchValue = 'http://localhost:4200';//Because i am running this project on port 4200
    const bc = new BroadcastChannel(matchValue);
    bc.onmessage = (event) => {
      console.log(event);
      if (event.data === `firstTab`) {
        bc.postMessage(`newTab`);
      }
      if (event.data === `newTab`) {
        alert('Application does not allow multiple tabs please close this tab ');
        window.open(window.location.origin, "_self");
        sessionStorage.removeItem('token')
      }
    };
    bc.postMessage(`firstTab`);
  }
}
