import { DatePipe, DecimalPipe } from '@angular/common';
import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { JwtHelperService } from '@auth0/angular-jwt';
import { isString } from 'lodash';

@Injectable({
  providedIn: 'root'
})
export class CommonService {
  isArray: boolean;
  fileTypes = ['image/jpeg', 'image/jpg', 'image/png', 'application/pdf'];//'application/pdf'
  constructor(
    private router: Router,
    private sanitizer: DomSanitizer,
    private _decimalPipe: DecimalPipe,
    private _datePipe: DatePipe
  ) { }
  transformDate(date, format = 'yyyy-MM-dd') {
    return this._datePipe.transform(date, format);
  }
  formatDateToISO(date): string | null {
    if (!date) return null;
    const newDate = new Date(date);
    // Create a new Date object with the local time (ignoring time zone conversion)
    const localDate = new Date(newDate.getTime() - newDate.getTimezoneOffset() * 60000);
    return localDate.toISOString().split('T')[0];
  }
  convertFormGroupToFormData(formGroup: FormGroup, preventAppendingControls?: string[]): FormData {
    // preventAppendingControls param sometime you don't need to append control to formdata then you need to pass that control name in that param;
    const formData = new FormData();
    Object.keys(formGroup.controls).forEach(controlName => {
      let isExists = preventAppendingControls?.includes(controlName);
      if (!isExists) {
        const control = formGroup.get(controlName);
        formData.append(controlName, control.getRawValue());
      }
    });
    return formData;
  }
  trim(value) {
    for (var key in value) {
      if (value[key] && isString(value[key])) {
        value[key] = (value[key]).trim();
      }
      else if (value[key] === null) {
        value[key] = '';
      }
    }
    return value;
  }
  getMonthEndDate() {
    var today = new Date();
    var endOfMonth = new Date(today.getFullYear(), today.getMonth() + 1, 0);
    return endOfMonth;
  }
  getMonthStartDate() {
    var today = new Date();
    var startOfMonth = new Date(today.getFullYear(), today.getMonth(), 1);
    return startOfMonth;
  }
  NavigateToRouteWithQueryString(routeName: string, queryParams?: any) {
    if (queryParams == undefined || queryParams == null)
      this.router.navigate([routeName]);
    else
      this.router.navigate([routeName], queryParams);
  }
  decodeToken(token?) {
    const helper = new JwtHelperService();
    return helper.decodeToken(token);
  }
  NavigateToRoute(routeName: string, params?: any) {
    if (params == undefined || params == null)
      this.router.navigate([routeName]);
    else
      this.router.navigate([routeName, params]);
  }
  numberOnly(event: any): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }
  charOnly(event: any): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if ((charCode > 47 && charCode < 58)) {
      return false;
    }
    return true;
  }
  omitSpecialChar(event: any): boolean {
    var k;
    k = event.charCode;
    return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32 || (k <= 48 && k >= 57));
  }
  stringToTime(time24) {
    if (time24) {
      var ts = time24;
      var H = +ts.substr(0, 2);
      var h: number | string = (H % 12) || 12;
      h = (h < 10) ? ("0" + h) : h;  // leading 0 at the left for 1 digit hours
      var ampm = H < 12 ? " AM" : " PM";
      ts = h + ts.substr(2, 3) + ampm;
      return ts;
    }
  };
  fileToBase64 = async (file: any) =>
    new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = () => {
        if (file.type !== 'application/pdf') {
          resolve({ fileType: file.type, base64: reader.result, file, fileName: file.name })
        }
        else {
          resolve({ fileType: file.type, file, fileName: file.name })
        }
      }
      reader.onerror = (e) => reject(e)
    })
  transform(base64Image: string) {
    return this.sanitizer.bypassSecurityTrustResourceUrl('data:image/jpeg;base64,' + base64Image);
  }
  transformDecimal(num, digitInfo = '2.3-3') {
    return this._decimalPipe.transform(num, digitInfo);
  }
  checkInvalidImageFormat(data, returnObj = false): any {
    let invalidError = '';
    let invalidExtentions = false;
    let files;
    let getFiles = data.map(x => {
      return x.file
    })
    if (getFiles[0] !== undefined) {
      files = getFiles;
    }
    else {
      files = data;
    }
    for (let file of files) {
      if (!this.fileTypes.includes(file.type)) {
        invalidExtentions = true;
        invalidError = 'This file not support, supported formates: JPEG, JPG, PNG, PDF';
        break;
      }
    }
    if (returnObj) {
      return { invalidExtentions, invalidError };
    }
    else {
      return invalidExtentions;
    }
  }
  roundValue(data, control) {
    if (Array.isArray(data)) {
      return data.map(x => {
        let value = {
          ...x, [control]: +(x[control].toFixed(3))
        }
        return value;
      })
    }
    else {
      let value = { ...data, [control]: +(data[control].toFixed(3)) }
      return value;
    }
  }
  remove(data, control) {
    if (Array.isArray(data)) {
      return data.map(x => {
        let value = {
          ...x, [control]: +(x[control].toFixed(3))
        }
        return value;
      })
    }
    else {
      let value = { ...data, [control]: +(data[control].toFixed(3)) }
      return value;
    }
  }
  replaceValue(value: string, splitBy, joinBy) {
    return value.split(',').join(joinBy);
  }
  downloadInvoices(data) {
    this.isArray = Array.isArray(data.invoices);
    if (this.isArray) {
      data.invoices.forEach(x => {
        this.iterateData(x);
      })
    }
    else {
      this.iterateData(data)
    }
  }
  iterateData(data) {
    if (this.isArray) {
      this.downloadURI(data);
    }
    else {
      for (let key in data) {
        let item = data[key];
        if (item) {
          setTimeout(() => {
            this.downloadURI(item)
          }, 500)
        }
      }
    }
  }
  downloadURI(uri: string) {
    //let name = uri.split('/').pop();
    var link = document.createElement("a");
    link.setAttribute('target', '_blank');
    link.href = uri;
    document.body.appendChild(link);
    link.click();
    link.remove();
  }
}
