import { Component, Input } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../services/common.service';

@Component({
  selector: 'app-download-invoices',
  templateUrl: './download-invoices.component.html',
  styleUrls: ['./download-invoices.component.scss'],
})
export class DownloadInvoicesComponent {
  @Input() data;
  constructor(public _activeModal: NgbActiveModal,
    private _modalService: NgbModal,
    private _commonService: CommonService
  ) { }
  ngOnChanges() {
    console.log();
  }
  ngOnInit() {
  }
  redirectTo() {
    if (this.data.redirectUrl) {
      this._commonService.NavigateToRoute(this.data?.redirectUrl);
    }
  }
  downloadInvoices() {
    let isArray = Array.isArray(this.data.invoices);
    if (isArray) {
      this.data.invoices.forEach(x => {
        this.iterateData(x);
      })
    }
    else {
      this.iterateData(this.data.invoices)
    }
    setTimeout(() => {
      this._modalService.dismissAll();
    }, 3000)
  }
  iterateData(data) {
    for (let key in data) {
      let item = data[key];
      if (item) {
        setTimeout(() => {
          this.downloadURI(item)
        }, 500)
      }
    }
  }
  downloadURI(uri: string) {
    let name = uri.split('/').pop();
    var link = document.createElement("a");
    link.setAttribute('target', '_blank');
    link.href = uri;
    document.body.appendChild(link);
    link.click();
    link.remove();
  }
}
