import { Component, Input } from '@angular/core';
import { ViewUploadedFileComponent } from '../../../dashboard/modules/main-fund/bank-deposits/view-uploaded-file/view-uploaded-file.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'view-file',
  templateUrl: './view-file.component.html',
  styleUrls: ['./view-file.component.scss']
})
export class ViewFileComponent {
  @Input() file;
  @Input() data;
  fileName: string;
  constructor(private _modalService: NgbModal) { }
  ngOnInit() {
    //this.fileName = this.getFileType(this.file);
  }
  fileAction(action) {
    if (action == 'view')
      this.openFileModal();
  }
  openFileModal() {
    this._modalService.open(ViewUploadedFileComponent).componentInstance.file = this.file;
  }
  getFileType(fileName) {
    return fileName?.split('/').pop();
  }
}
