import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../services/common.service';
@Component({
  selector: 'app-modal-message',
  templateUrl: './modal-message.component.html',
  styleUrls: ['./modal-message.component.scss'],
})
export class ModalMessageComponent implements OnInit {
  @Input() name;
  @Input() type: string;
  @Input() message: string;
  @Input() messageError: any;
  @Input() routeName: any;
  @Input() data: any;
  @Input() dismissOnlyActive: boolean;
  @Output() loadPageData = new EventEmitter<string>();

  @Input() errors;

  constructor(
    private router: Router,
    private modalService: NgbModal,
    public activeModal: NgbActiveModal,
    public _commonService: CommonService,

  ) { }
  ngOnInit(): void {
    //console.log(this.type, this.message, this.messageError);
    if (this.type && this.type == 'success') {
      setTimeout(() => {
        if (this.routeName) {
          this.router.navigateByUrl(this.routeName);
        }
        this.toLoadData();
        if (!this.dismissOnlyActive)
          this.modalService.dismissAll();
        else
          this.activeModal.dismiss();
      }, 5000);
    } else if (this.type && this.type == 'error') {
    }
  }
  toLoadData() {
    this.loadPageData.emit();
  }
}
